.noticias{
    display: flex;
    width: 90vw;
    height: 300px;
    overflow-x: auto;
    margin: auto;
    justify-content: space-around;
}
h2{
    background-color: blue;
    color: white;
    width: 100%;
    text-indent: 15px;
}
.noticias div{
    margin-top: 30px;
    background-color: white;
    box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.363);
    height: 240px;
    width: 290px;
    padding: 3px;
}
.noticias img{
    height: 150px;
    width: 250px;
    margin-top: 5px;
    margin-left: 15px;
}
.noticias h4{
    font-size: 15px;
    margin: auto;
    margin-left: 5px;
    justify-content: left;
    
}
.noticias a{
    color: black;
    text-decoration: none;
}
@media(max-width: 450px){
    .noticias {
        justify-content: flex-start;
    }
    
}